import React from 'react';
import {withStyles, makeStyles} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Checkbox from '@material-ui/core/Checkbox';

const StyledTableCell = withStyles(theme => ({
    head: {
        backgroundColor: theme.palette.common.black,
        color: theme.palette.common.white,
    },
    body: {
        fontSize: 14,
    },
}))(TableCell);

const StyledTableRow = withStyles(theme => ({
    root: {
        '&:nth-of-type(odd)': {
            backgroundColor: theme.palette.background.default,
        },
    },
}))(TableRow);

const useStyles = theme => ({
    table: {},
});

function createData(name, calories, fat, carbs, protein) {
    return {name, calories, fat, carbs, protein};
}

const rows = [
    createData('Frozen yoghurt', 159, 6.0, 24, 4.0),
    createData('Ice cream sandwich', 237, 9.0, 37, 4.3),
    createData('Eclair', 262, 16.0, 24, 6.0),
    createData('Cupcake', 305, 3.7, 67, 4.3),
    createData('Gingerbread', 356, 16.0, 49, 3.9),
];

class Posts extends React.Component {
    constructor(props) {
        super(props);
        this.state = {'posts': [], 'page': 1}
    }

    componentDidMount() {
        this.fetchData()
    }

    fetchData() {
        fetch("http://localhost:5000/feed?page=" + this.state.page)
            .then(res => res.json())
            .then(
                (result) => {
                    console.log(result)
                    this.setState({'posts': result, 'page': this.state.page++})
                },
                (error) => {
                    console.log(error)
                }
            )
    }

    updatePost(twitter_id, validated) {
        fetch("http://localhost:5000/feed", {
            method: 'POST',
            body: JSON.stringify({
                    twitter_id: twitter_id,
                    validated: validated
                }
            ),
            headers: {'Content-Type': 'application/json'}
        })
            .then(res => res.json())
            .then(
                (result) => {
                    console.log(result)
                },
                (error) => {
                    console.log(error)
                }
            )
    }

    render() {
        const {classes} = this.props;

        return (
            <div>
                <h3>Posts module</h3>

                <TableContainer component={Paper}>
                    <Table className={classes.table} aria-label="customized table">
                        <TableHead>
                            <TableRow>
                                <StyledTableCell>Text</StyledTableCell>
                                <StyledTableCell align="right">Link</StyledTableCell>
                                <StyledTableCell align="right">User</StyledTableCell>
                                <StyledTableCell align="right">Creation Date</StyledTableCell>
                                <StyledTableCell align="right">Enabled</StyledTableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {this.state.posts.map(row => (
                                <StyledTableRow key={row.twitter_id}>
                                    <StyledTableCell component="th" scope="row">
                                        {row.title}
                                    </StyledTableCell>
                                    <StyledTableCell align="right">
                                        <a href={row.link}>Ver</a>
                                    </StyledTableCell>
                                    <StyledTableCell align="right">{row.user.profile.twitter_name}</StyledTableCell>
                                    <StyledTableCell align="right">{row.created_at.$date}</StyledTableCell>
                                    <StyledTableCell align="right">
                                        <Checkbox
                                            checked={row.validated}
                                            color="primary"
                                            /*onChange={handleChange}*/
                                            value="primary"
                                            inputProps={{'aria-label': 'primary checkbox'}}
                                            onChange={this.updatePost(row.twitter_id, row.validated)}
                                        />
                                    </StyledTableCell>
                                </StyledTableRow>
                            ))}
                        </TableBody>
                    </Table>
                </TableContainer>
            </div>
        )
    }
}

export default {
    routeProps: {
        path: '/posts',
        component: withStyles(useStyles, {withTheme: true})(Posts),
    },
    name: 'Posts',
};
